import {createSlice} from '@reduxjs/toolkit'
import { toast } from 'react-toastify';


const items=

localStorage.getItem('cartItems') !== null ?
JSON.parse(localStorage.getItem('cartItems'))
:[];


const totalAmount=

localStorage.getItem('totalAmount') !== null ?
JSON.parse(localStorage.getItem('totalAmount'))
:[];

const totalQuantity=

localStorage.getItem('totalQuantity') !== null ?
JSON.parse(localStorage.getItem('totalQuantity'))
:[];

const initialState={
    cartItems:items,
    totalQuantity:totalQuantity,
    totalAmount:totalAmount

}

const cartSlice=createSlice({
    name:'cart',
    initialState:initialState,

    reducers:{

        addItem(state,action){
            const newItem= action.payload
            const existingItem = state.cartItems.find(item => item.id===newItem.id)
            state.totalQuantity++
            toast.success('Added To Cart :)', {
                position: "top-right",
                autoClose: 1000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                });

            if(!existingItem){

                state.cartItems.push({
                    id:newItem.id,
                    title:newItem.title,
                    image01:newItem.image01,
                    price:newItem.price,
                    quantity:1,
                    totalprice:newItem.price
                })
                
            }
            else{
                existingItem.quantity++
                existingItem.totalprice=Number(existingItem.totalprice)
                + Number(newItem.price)
            }
            state.totalAmount= state.cartItems.reduce(
                (total,item)=> total + Number(item.price) * Number(item.quantity),0
          
            )

            localStorage.setItem(
                'cartItems',
                JSON.stringify(state.cartItems.map((item)=>item))
                );
   
   
                localStorage.setItem(
                   'totalAmount',
                   JSON.stringify(state.totalAmount)
                   );
               
   
                   localStorage.setItem(
                       'totalQuantity',
                       JSON.stringify(state.totalQuantity)
                       ); 

              
            
        },
        
        removeItem(state,action){
            const id=action.payload
            const existingItem=state.cartItems.find(item => item.id===id)
            state.totalQuantity--

            if(existingItem.quantity===1){
                state.cartItems=state.cartItems.filter(item=> item.id !== id)
            }
            else{
                existingItem.quantity--
                existingItem.totalprice=Number(existingItem.totalprice)-Number
                (existingItem.price)
            }
            state.totalAmount= state.cartItems.reduce(
                (total,item)=> total+ Number(item.price) * Number(item.
                    quantity),
                    0
            )

            localStorage.setItem(
                'cartItems',
                JSON.stringify(state.cartItems.map((item)=>item))
                );
   
   
                localStorage.setItem(
                   'totalAmount',
                   JSON.stringify(state.totalAmount)
                   );
               
   
                   localStorage.setItem(
                       'totalQuantity',
                       JSON.stringify(state.totalQuantity)
                       ); 
        },

        deletItem(state,action){
            const id =action.payload
            const existingItem=state.cartItems.find(item => item.id===id)

            if(existingItem){
                state.cartItems= state.cartItems.filter(item => item.id !==id)
                state.totalQuantity=state.totalQuantity- existingItem.quantity
            }

            state.totalAmount= state.cartItems.reduce(
                (total,item)=> total+ Number(item.price) * Number(item.
                    quantity),
                    0
            )

            localStorage.setItem(
                'cartItems',
                JSON.stringify(state.cartItems.map((item)=>item))
                );
   
   
                localStorage.setItem(
                   'totalAmount',
                   JSON.stringify(state.totalAmount)
                   );
               
   
                   localStorage.setItem(
                       'totalQuantity',
                       JSON.stringify(state.totalQuantity)
                       ); 
                
            

           

        }

    }
})

 

export const cartActions= cartSlice.actions
export default cartSlice